<script>
import Login from '@/components/LogInComponent.vue'
import Register from '@/components/RegisterComponent.vue'

export default {
    name: 'Slider',
    components: {
      Login,
      Register,
    },
    data() {
      return {
        isLoginShowed: false,
        isRegisterShowed: false,
      }
    },
    methods: {
      showLogin() {
        this.isLoginShowed = !this.isLoginShowed;
        this.isRegisterShowed = false;
      },
      showRegister() {
        this.isRegisterShowed = !this.isRegisterShowed;
        this.isLoginShowed = false;
      },
      toggleShowed() {
        this.isRegisterShowed = !this.isRegisterShowed;
        this.isLoginShowed = !this.isLoginShowed;
      },

    }
  }
</script>

