import axios from 'axios';

// const url = 'http://localhost:5000/api/announcements/';
const url = 'api/announcements/';

class AnnouncementsService {
    //get Posts to accept
    static getNotAcceptedAnnouncements() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}notaccepted/null`);
                const data = res.data;

                resolve(
                    data.map(announcement => ({
                        ...announcement,
                        createdAt: new Date(announcement.createdAt)
                    }))
                )
            } catch(err) {
                reject(err)
            }
        });
    };

    static getAnnouncementsFromCategory(category) {
        console.log('jestem tutaj ezzz');
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}accepted/${category}`);
                const data = res.data;
                console.log('getAnnouncementsFromCategory');

                resolve(
                    data.map(announcement => ({
                        ...announcement,
                        createdAt: new Date(announcement.createdAt)
                    }))
                )
            } catch(err) {
                reject(err)
            }
        });
    }

    // get posts 
    static getAnnouncements() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}accepted/all`);
                const data = res.data;

                resolve(
                    data.map(announcement => ({
                        ...announcement,
                        createdAt: new Date(announcement.createdAt)
                    }))
                )
            } catch(err) {
                reject(err)
            }
        });
    };

    //create Posts
    static insertAnnouncement(img, title, text, category) {
        return axios.post(url, {
            img,
            title,
            text,
            category
        });
    }

    //update Post
    static updateAnnouncement(id) {
        return axios.put(`${url}${id}`);
    }

    //delete Post
    static deleteAnnoucement(id) {
        return axios.delete(`${url}${id}`);
    }
}

export default AnnouncementsService;