<template>
  <div class="home">
    <b-container>
      <b-row class="d-flex justify-content-center">
        <b-col class="slides ">
          <div class="d-block ml-3">
            <b-carousel
              id="carousel"
              v-model="slide"
              :interval="10000"
              controls
              indicators
              background="#ababab"
              img-width="1024"
              img-height="480"
              style="text-shadow: 1px 1px 2px #333;"
              @sliding-start="onSlideStart"
              @sliding-end="onSlideEnd"
            >
              <sliderComponent v-for="slide in sliderData" 
                :key="slide.id"
                :title="slide.title"
                :urlToImage="slide.urlToImage"
                :text="slide.description"
                :link="slide.url"
              > </sliderComponent>


            </b-carousel>
          </div>
        </b-col>
      </b-row>

    <div class='news'>
      <b-row>
        <b-col>
            <div class="sub-title">
                <h1> Wiadomości </h1>
            </div>
        </b-col>
      </b-row>
        <b-row class="justify-content-center">
          <newsComponent v-for="news in newsData"
          :key="news.id"
          :title="news.title"
          :urlToImage="news.urlToImage"
          :text="news.description"
          :link="news.url"
          :content="news.content"
          > </newsComponent>

        </b-row>
  </div>
  
    <div class='announc'>
      <b-row>
        <b-col>
            <div class="sub-title">
                <h1> Ogłoszenia </h1>
            </div>
        </b-col>
      </b-row>
        <b-row class="justify-content-center">

          <announComponent v-for="announcement in announcements"
          :key="announcement._id"
          :title="announcement.title"
          :img="announcement.img"
          :content="announcement.text"
          > </announComponent>

        </b-row>
  </div>
    </b-container>
  <FooterComponent />
  </div>
</template>

<script>
// @ is an alias to /src
import newsComponent from '@/components/NewsComponent.vue';
import announComponent from '@/components/AnnounComponent.vue';
import FooterComponent from '@/components/FooterComponent.vue';
import sliderComponent from '@/components/SliderComponent.vue';

import AnnouncementsService from '../AnnouncementsService.js';


export default {
  name: 'Home',
  components: {
    sliderComponent,
    newsComponent,
    announComponent,
    FooterComponent,
  },
// slider items
    data() {
      return {
        // slider
        slide: 0,
        sliding: null,
        // data
        data: [],
        sliderData: [],
        newsData: [],
        announcements: [],
        announcementsData: [],
        error: ''
      }
    },
    async created() {
      try {
        this.announcements = await AnnouncementsService.getAnnouncements();
        this.announcements.splice(6, this.announcements.length)
      } catch(err) {
        this.error = err.message;
        if(error) {
          console.log(this.error);
        }
      }
    },
    
  mounted() {
    this.fillNews();
  },

    methods: {
      onSlideStart() {
        this.sliding = true
      },
      onSlideEnd() {
        this.sliding = false
      },
      // fill data
      async fillNews() {
         const res = await fetch("data.json");
         const val = await res.json();
         this.data = [...val[0].articles];
         this.sliderData = [...val[0].articles];
         this.newsData = [...val[0].articles];

        this.sliderData.splice(5, this.sliderData.length)

        this.newsData.splice(0, 5)
        this.newsData.splice(6, this.newsData.length)

        // console.log(this.newsData);
      }
    }
  
}
</script>

