<template>
  <div class="newsComponent">
      
          <b-col>
            <b-card @click="showNews"
                :title="title"
                :img-src="urlToImage"
                img-alt="Image"
                img-top
                tag="article"
                style="max-width: 20rem;"
                class="mb-3 "
            >
                <b-card-text>
                    {{ text }}
                </b-card-text>
            </b-card>  
          </b-col>

        <div v-if="isShowed" class="newsBg" @click.self="showNews"> 
          <div class="showedNews" :style="pageY">
            <span class="d-flex justify-content-end pb-3" > <span class="closeIco" @click="showNews"> <i class="fas fa-times"></i> </span> </span>
            <img :src="urlToImage" alt="">
            <h2 class="mt-3 ml-5 mr-5"> {{ title }}</h2>
            <p class="mt-3 ml-5 mr-5"> {{ content }} </p>

            <h6> Więcej <a :href="link" target="_blank"> tutaj </a> </h6> 
          </div>
        </div>
  </div>
</template>

<script>
export default {
  name: 'newsComponent',
  props: ['title', 'urlToImage', 'text', 'link', 'content'],
  data () {
    return {
      isShowed: false,
      pageY: '',
    }
  },
  methods: {
    showNews(e) {
      this.pageY ="top: " + (e.pageY - e.y ) + "px"
      this.isShowed = !this.isShowed;
    }
  },
}
</script>

<style scoped>
    .newsBg {
      position: absolute;
      top: 115px;
      left:0;
      z-index: 10;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.39);
      padding-bottom: 672vh;
      cursor: pointer;
    }

    .showedNews {
      position: absolute;
      background-color: wheat;
      margin-top: 10px;
      border-radius: 10px;
      width: 60%;
      left: 20%;
      padding-top: 20px;
      padding-bottom: 150px;
      cursor: default;
    }

    .showedNews h2 {
      padding-left: 15%;
      padding-right: 15%;
    }

    .showedNews p {
      padding-left: 5%;
      padding-right: 5%;
      padding-top: 20px;
      font-size: 20px;
    }
    
    .showedNews img {
      width: 70%;
      border-radius: 5px;
    }

    .closeNews {
      display: flex;
      justify-content: flex-end;
      margin-right: 25px;
      padding-bottom: 15px;
      font-size: 30px;
      cursor: pointer;
      color: #3f3f3f;
    }

    .closeIco {
      margin-right: 25px;
      padding: 5px;
      width: 30px; 
      font-size: 30px;
      cursor: pointer;
      color: #3f3f3f;
    }

    .closeIco:hover {
      color: #202020;
    }

    @media(max-width: 450px) {
      .showedNews {
        width: 80%;
        left: 10%;
      }
      .showedNews h2 {
        padding-left: 2%;
        padding-right: 2%;
      }

      .showedNews p {
        padding-left: 1%;
        padding-right: 1%;
      }
    }
</style>
