<template>
  <div class="LoginComponent">
      <div class="loginModuleBg" @click.self="closeModal">
          <div class="loginModule">

            <span class="closeBox" > <span class="closeIco" @click="closeModal"> <i class="fas fa-times"></i> </span> </span>

            <b-card
                header="Zaloguj się!"
                header-bg-variant="primary"
                header-text-variant="white"
                class="loginForm"
            >
                <b-card-text>
                    <b-form @submit="onSubmit">
                        <b-form-group
                            description="Podaj swoją nazwę użytkownika."
                            label="Nazwa użytkownika"
                        > 
                        <b-form-input v-model="username" required> </b-form-input>
                        </b-form-group> 
                        <b-form-group
                            description="Podaj swoje hasło."
                            label="Hasło"
                        > 
                        <b-form-input v-model="password" type="password" required> </b-form-input>
                    </b-form-group> 
                    <b-form-group>
                        <h6 class="h6Text" >Nie masz konta? Zarejestruj się <span @click="openRegister">tutaj.</span></h6>
                    </b-form-group>
                    <b-form-group class="text-center">
                        <b-button type="submit" variant="outline-primary" :disabled="acceptableSubmittion" >Zaloguj się</b-button>
                    </b-form-group>
                    </b-form>
                </b-card-text>
                
            </b-card>
          </div>
      </div>
  </div>
</template>

<script>

import UsersService from '../UsersService.js';

export default {
    name: 'LoginComponent',

    data() {
        return {
            username: '',
            password: '',
        }
    },
    methods: {
        onSubmit (e) {
            e.preventDefault();
            // console.log(this.username);
            // console.log(this.password);
            UsersService.getUser(this.username, this.password)

        },
        closeModal() {
            this.$emit('close');
        },
        openRegister() {
            this.$emit('toggle')
        },
    },
    computed: {

        acceptableSubmittion() {
            return this.username.length > 2 && this.password.length > 3 ? false : true;
        }
    }

}
</script>

<style>
    .loginModuleBg {
        position: fixed;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.4);
        top: 0;
        left: 0;
        cursor: pointer;
    }

    .loginModule {
        position: relative;
        background-color: #e2ebec;
        border-radius: 1.5rem;
        top: 130px;
        left: 10%; right: 10%;
        width: 80%;
        height: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: default;
    }

    .card-header {
        text-align: center;
    }


    .loginForm {
        width: 60%;
        position: relative;
        border-radius: 1rem;
        text-align: left;
        margin-top: 45px;
        margin-bottom: 15px;
    }

    span {
        color: #007BFF;
        cursor: pointer;
    }

     .closeBox {
        position: absolute;
        display: flex;
        top: 15px;
        right: 30px;
        z-index: 5;
    }

     .closeIco {
      padding: 5px;
      width: 30px; 
      font-size: 25px;
      cursor: pointer;
      color: #3f3f3f;
    }

    .closeIco:hover {
        color: #202020;
    }

    @media(max-width: 450px) {

        .loginModule {
            left: 5%; right: 5%;
            width: 90%;
         }
     
        .loginForm {
            width: 85%;
        }

        .h6Text {
            margin-right: 100px;
        }
    }
</style>